import React, { useState, useEffect } from 'react'
import { Paper, Box, Tooltip } from '@material-ui/core'
import MaterialTable, { MTableToolbar } from 'material-table'
import StartEndDatePicker from '../datePicker/StartEndDatePicker'
import clsx from 'clsx'
import produce from 'immer'

import './table.scss'

const Table = ({ term = false, termColumn = '', scrollable = false, components = {}, label = '', options = {}, localization = {}, isAddToolbar = false, AddToolbar = {}, AddToolbarProps = {}, data = [], ...props }) => {
  const [searched, setSearched] = useState([])
  const [filter, setFilter] = useState({
    start: null,
    end: null
  })

  const handleChangeDate = (start, end) => {
    if (props.setDate) {
      props.setDate({ start, end })
    }
    setFilter({ start, end })
  }

  useEffect(() => {
    setSearched(data.filter(item => (
      (!filter.start || filter.start <= new Date(item[termColumn]))
      && (!filter.end || filter.end >= new Date(item[termColumn]))
    )))
  }, [data, filter.start, filter.end])

  return (
    <MaterialTable
      data={searched}
      components={{
        Container: props => (
          <Paper {...props} className={clsx('custom_table', scrollable && 'scrollable')} />
        ),
        Toolbar: props => (
          <div className="toolbar">
            <Box display="flex" alignItems="center">
              <MTableToolbar {...props} />
              
              {isAddToolbar && (
                <AddToolbar {...AddToolbarProps}></AddToolbar>
              )}
            </Box>

            {term && (
              <StartEndDatePicker
                start={filter.start}
                end={filter.end}
                onChange={handleChangeDate}
              />
            )}
          </div>
        ),
        ...components
      }}
      localization={{
        body: {
          emptyDataSourceMessage: '  .',
          filterRow: {
            filterTooltip: ''
          },
          editRow: {
            saveTooltip: '',
            cancelTooltip: '',
            deleteText: '   ?'
          },
          addTooltip: '',
          deleteTooltip: '',
          editTooltip: ''
        },
        grouping: {
          placeholder: '   '
        },
        toolbar: {
          searchTooltip: '',
          searchPlaceholder: '',
          exportTitle: ' ',
          exportName: 'CSV ',
          showColumnsTitle: '',
          addRemoveColumns: '   '
        },
        pagination: {
          labelRowsSelect: `${label}`,
          firstTooltip: ' ',
          previousTooltip: ' ',
          nextTooltip: ' ',
          lastTooltip: ' ',
          labelDisplayedRows: `{count}${label}  {from}~{to}${label}`
        },
        ...localization
      }}
      options={{
        draggable: false,
        search: false,
        doubleHorizontalScroll: true,
        columnsButton: true,
        ...options
      }}
      {...props}
    />
  )
}

export default Table